import React, {Component} from 'react';
import PropTypes from 'prop-types';
import {inject, observer} from 'mobx-react';


import {Button, withStyles} from "@material-ui/core";
import Typography from '@material-ui/core/Typography';
import Modal from '@material-ui/core/Modal';
import SendIcon from '@material-ui/icons/Send';


import WysiwygEditor from "./WysiwygEditor";

const styles = theme => ({
    paper: {
        position: 'absolute',
        width: theme.spacing.unit * 100,
        height: theme.spacing.unit * 70,
        backgroundColor: theme.palette.background.paper,
        boxShadow: theme.shadows[5],
        padding: theme.spacing.unit * 4,
        outline: 'none',
        top: '50%',
        left: '50%',
        transform: 'translate(-50%, -50%)',
    },
    sendIcon:{
        marginLeft: theme.spacing.unit
    }
});


@inject('navState')
@inject('createReplyActionState')
@observer
class CreateReplyModal extends React.Component {

    render() {
        const {classes, navState, createReplyActionState} = this.props;

        return (
            <Modal
                aria-labelledby="simple-modal-title"
                aria-describedby="simple-modal-description"
                open={createReplyActionState.isModalEditorOpen}
                onClose={createReplyActionState.setModalEditorClose}
            >
                <div className={classes.paper}>
                    <Typography variant="h6" id="modal-title">
                        {navState.getModalEditorTitle}
                    </Typography>
                    <WysiwygEditor/>
                </div>
            </Modal>
        );
    }
}

export default withStyles(styles)(CreateReplyModal);